body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.app {
  display: flex;
  flex-direction: column;
}

.gray {
  color: #b7b7b7;
}

.bold {
  font-weight: bold;
}

.header {
    display: flex;
    box-shadow: 0 0 10px #00000077;
    padding: 10px 20px;
    
}

.logo {
  width: 80px;
}

.hero {
  padding: 30px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;

 
}
.hero>* {
  margin: 20px 0;
}

.hero h2, .hero p {
  text-align: left;
}

.hero img {
  width: 80%;
  margin: 15px auto;
}

.hero-img {
  display: flex;
}
.hero-img>h2{
  padding-top: 20px;
}
.card {
  position: relative;
  width: 175px;
  font-size: 12px;
  margin-right: 30px;
  display: flex;
  flex-direction: column;
  flex: 0 0 auto;
}

.star {
  width: 10px;
}

.card>*{
  margin-bottom: 5px;
}

.card-image {
  width: 100%;
  border-radius: 10px;
}

.card-stats {
  display: flex;
  align-items: center;

}

.card-list {
  display: flex;
  overflow-x: scroll;
  margin: 0 30px;

}

.card-badge {
  position: absolute;
  background: #fff;
  left: 5px;
  top:5px;
  padding: 5px 7px;
  border-radius: 4px;
  font-weight: bold;
}